#navbar-container {
	background-color: white;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	box-shadow: 3px 3px 3px rgba(0,0,0,0.3);
}

#navbar-content {
	font-family: "Roboto", "Trebuchet MS", sans-serif;
	display: flex;
	justify-content: space-evenly;
	max-width: 65%;
	margin: auto;
}


#main-content {
	border-radius: 25px;
	border: 1px solid #E7E9EB;
	background-color: white;
	padding: 10px 30px 30px 30px;
	min-height: calc(100vh - 100px);
	max-width: 65%;
	margin: auto;
	margin-top: 10px;
}

.home-page-body {
	/*font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;*/
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-size: 16px;
}


.about-header {
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-weight: bold;
}

.about-page-body {
	/*font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;*/
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-size: 16px;
}


.clickable {
	cursor: pointer;
}


.project-header {
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-weight: bold;
}

.project-description {
	/*font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;*/
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-size: 16px;
}

#project-buttons {
	display: flex;
	justify-content: space-evenly;
	padding: 1em 30px 0px 30px;
}

html {
